import { useEffect } from "react"
import ReactDOM from "react-dom/client"
import { HashRouter, Routes, Route } from "react-router-dom"
import { Helmet } from 'react-helmet'
import "./data/i18n"
import "./assets/style.css"


import Home from "./pages/Home"
import Cv from "./pages/Cv"
import ReactjsTest from "./pages/ReactjsTest"
import Duan from "./pages/Duan"
import AdministratorZ from "./pages/AdministratorZ"
import NoPage from "./pages/NoPage"



const MetaTitle = ({ title, children }) => {
    useEffect(() => {
        document.title = title;
    }, [title]);

    return children;
};


export default function App() {
    return (
        <HashRouter>
            <Routes>
                <Route path="/"element={<MetaTitle title="Trang ch"> <Home /> </MetaTitle> } /> 
                <Route path="/duan"element={<MetaTitle title="D n"> <Duan /> </MetaTitle> } /> 
                <Route path="/cv"element={<MetaTitle title="L Vn Qu - Wordpress Dev CV"> <Cv /> </MetaTitle> } /> 
                <Route path="/reactjstest"element={<MetaTitle title="ReactJS Test"> <ReactjsTest /> </MetaTitle> } /> 
                <Route path="/administratorz"element={<MetaTitle title="AdministratorZ"> <AdministratorZ /> </MetaTitle> } /> 
                <Route path="*"element={<MetaTitle title="Khng tm thy trang"> <NoPage /> </MetaTitle> } /> 
            </Routes>
        </HashRouter> 
    )
}



const root = ReactDOM.createRoot(document.getElementById('root'))
root.render(<App/>)